:root {
    /* Colours */
    --blue: #3eb0ef;
    --green: #a4d037;
    --purple: #ad26b4;
    --yellow: #fecd35;
    --red: #f05230;
    --darkgrey: #263238;
    --midgrey: #738a94;
    --lightgrey: #c5d2d9;
    --whitegrey: #BEBEBE	;
    --darkergrey: #404040;
    --pink: #fa3a57;
    --brown: #a3821a;
    --darkslate: darkslategray;
    --darkmode: var(--darkgrey);  
   
   --shadow-base: 0 0 30px rgba(0,0,0,.35);
}

 body {
/*     color: rgba(255, 255, 255, 0.75);*/
     background-color: var(--darkmode);
 }

.site-header{
   box-shadow: var(--shadow-base);
   background-color: var(--darkmode);
   position:fixed;
   width:100%;
}
.site-header a {
   color: rgba(255, 255, 255, 0.75);
}
.site-content{
   padding-top: 80px;
}

.post-wrapper{
   background-color: var(--darkslate);
}

a.post-title-link{
   color: var(--light-gray-color);
}

.u-shadow {
   box-shadow: var(--shadow-base);
}

.u-shadow:hover {
   box-shadow: 0 0 20px rgba(128,128,128,.35);

}

.post-footer {
   background-color: var(--lightgrey);
}

span.post-card-meta-date {
    float: right;
    color: var(--lightgrey);
    font-style: italic;
    font-size: 0.75rem;
}

span.post-card-meta-tag a, .widget-tags .tag-count {
   background-color: var(--blue);
   color:var(--primary-text-color);
}

.widget {
   background-color: var(--darkergrey);
   border:1px solid rgba(0,0,0,.5);
}

.widget-tags .tag-name {
   color:var(--lightgrey);
}

.widget-tags .tag-name:hover {
   color:var(--blue);
}

.widget-tags .widget-action{
   float: right;
   color:inherit;
}

.widget-tags .widget-action:hover{
   color:var(--blue);
}